/* styles.css */

h4 {
    text-align: center;
    color: #1976D2;
    margin: 5px 5px 5px 5px;
  }
  

  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    min-height: 100vh;
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #dcdcdc;
  }
  
  .menuQr {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 80%;
    margin-top: 20px;
  }
  
.tarjeta {
  width: 45%;
  margin: 20px;
  box-sizing: border-box;
  border-radius: 10px;
  background-color:  #f0f0f0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

img {
  width: 100%;
  max-width: 100%; /* Establece un ancho máximo en lugar de un alto máximo */
  height: auto;
  object-fit: cover;
  border-radius: 10px 10px 0 0;
}
  .text-container {
    padding: 20px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    flex: 1;
}

h2, h3 {
    margin-bottom: 20px; /* Aumenta el margen inferior de los títulos */
    font-family: Arial, sans-serif;
    text-align: center;
}



h3 {
  margin-top: 5px; /* Ajustar el margen superior de h3 */
  color: #24a5ba;
}

 
  @media only screen and (max-width: 600px) {
    .menuQr {
        width: 90%;
    }
  
    .tarjeta {
        width: 100%;
        margin: 20px auto; /* Centra la tarjeta horizontalmente */
    }
  
    img {
        height: auto;
        max-height: 40vh;
    }
}

